.advantages-card {
	text-align: center;
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-bottom: 60px;
	&__icon {
		width: 74px;
		height: 74px;
		border-radius: 50%;
		background-color: $brown;
		display: flex;
		align-items: center;
		justify-content: center;
		align-content: center;
		margin-bottom: 15px;
		svg {
			width: 42px;
			height: 42px;
			color: #fff;
		}
		&::after {
			content: '';
			width: 80px;
			height: 80px;
			position: absolute;
			border-radius: 50%;
			border: 1px solid $brown;
		}
	}
	&__title {
		@extend %card-title_1;
		text-transform: none;
		margin-bottom: 10px;
	}
	&__text {
		max-width: 263px;
		margin-right: auto;
		margin-left: auto;
		@extend %card-text_2;
	}
}
