.header {
	padding: 25px 0;
	position: relative;
	z-index: 3;
	&__logo {
		width: 263px;
		height: 30px;
		display: flex;
		align-items: center;
		justify-content: center;
		align-content: center;
		svg {
			width: 100%;
			height: 100%;
		}
	}
	&__row {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
}

.header-menu {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	align-content: flex-start;
	@extend %ul-reset;
	&__item {
		margin-right: 35px;
		&:last-child {
			margin-right: 0;
		}
	}
	&__link {
		display: flex;
		align-items: center;
		justify-content: center;
		align-content: center;
		padding: 10px 0;
		@extend %header-text;
		text-decoration: none;
		position: relative;
		&::after,
		&::before {
			content: '';
			height: 2px;
			background-color: $brown;
			position: absolute;
			width: 0;
			left: 0;
			display: block;
			transition: 0.3s all;
		}
		&::after {
			bottom: 0;
		}
		&::before {
			top: 0;
		}
		&:hover {
			&::after,
			&::before {
				width: 100%;
			}
		}
	}

