.btn {
	&-primary {
		display: flex;
		align-items: center;
		justify-content: center;
		align-content: center;
		text-decoration: none;
		background-color: transparent;
		border: none;
		outline: none;
		@extend %btn-text;
		cursor: pointer;
	}
	&-secondary {
		display: flex;
		align-items: center;
		justify-content: center;
		align-content: center;
		text-decoration: none;
		background-color: transparent;
		@extend %btn-text_2;
	}
	&-size-1 {
		padding: 15px 61px 13px 61px;
		min-width: 195px;
		width: fit-content;
		border-radius: 20px;
	}
	&-size-2 {
		padding: 14px 30px 12px 30px;
		border-radius: 20px;
		width: fit-content;
		min-width: 280px;
	}
	&-size-3 {
		padding: 12px 30px;
		border-radius: 15px;
		width: fit-content;
		min-width: 150px;
	}
	&-size-4 {
		padding: 17px 40px 16px 40px;
		border-radius: 20px;
		width: fit-content;
		min-width: 270px;
		border-radius: 20px;
	}
	&-size-5 {
		padding: 14px 30px;
		min-width: 210px;
		width: fit-content;
		border-radius: 20px;
	}
	&-no-size {
		padding: 16.5px 20px;
		border-radius: 20px;
		width: 100%;
	}
	&-brown {
		background-color: $brown;
		color: #fff;
		transition: 0.3s all;
		&:hover {
			background-color: #645450;
		}
	}
	&-border {
		color: $brown;
		border: 1px solid $brown;
		transition: 0.3s all;
		&:hover {
			background-color: #645450;
			color: #fff;
		}
	}
}

.btn-next {
	width: 51px;
	height: 51px;
	display: flex;
	align-items: center;
	justify-content: center;
	align-content: center;
	border-radius: 50%;
	border: none;
	outline: none;
	cursor: pointer;
	transform: translateY(-50%);
	background-color: rgba(202, 150, 82, 0.6);
	svg {
		width: 18px;
		height: 18px;
	}
	&::after {
		content: '';
		background-image: url(../img/line-btn.svg);
		width: 60px;
		height: 60px;
		position: absolute;
		background-repeat: no-repeat;
		background-position: center center;
		background-size: 100% 100%;
		left: 0;
		top: -4.5px;
		transition: 0.3s all;
		transform-origin: center;
	}

	&:hover {
		&::after {
			left: -9px;
			top: -4.5px;
			transform: rotate(180deg);
		}
	}
}
.btn-play {
	width: 80px;
	height: 80px;
	display: flex;
	align-items: center;
	justify-content: center;
	align-content: center;
	border-radius: 50%;
	border: none;
	position: relative;
	outline: none;
	cursor: pointer;
	transform: translateY(-50%);
	z-index: 5;
	background-color: rgba(202, 150, 82, 0.6);
	svg {
		width: 22px;
		height: 30px;
		color: #fff;
	}
	&::after {
		content: '';
		background-image: url(../img/line-btn_2.svg);
		width: 110px;
		height: 110px;
		position: absolute;
		background-repeat: no-repeat;
		background-position: center center;
		transition: 0.3s all;
		transform-origin: center;
		transform: rotate(0);
		z-index: -1;
	}
	&:hover {
		&::after {
			transform: rotate(180deg);
		}
	}
	&_dark {
		background-color: $brown;
	}
}

.btn-scroll-down {
	display: flex;
	align-items: center;
	justify-content: center;
	align-content: center;
	width: 36px;
	height: 36px;
	svg {
		color: #fff;
		width: 100%;
		height: 100%;
	}
}
