.advantages {
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	padding: 95px 0 35px 0;
	position: relative;
	margin-bottom: 120px;
	&__gradient {
		z-index: 2;
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		background: linear-gradient(
			180deg,
			rgba(196, 196, 196, 0) 7.29%,
			#c4c4c4 100%
		);
	}
	.container {
		position: relative;
		z-index: 3;
	}

	.section-title {
		margin-bottom: 60px;
	}
	&__row {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		flex-wrap: wrap;
	}
	&-card {
		width: 33.33%;
	}
}
