.footer {
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	padding: 30px 0;
	position: relative;
	&::after {
		content: '';
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		background: rgba(31, 24, 22, 0.7);
		backdrop-filter: blur(13px);
	}
	.row {
		position: relative;
		z-index: 2;
	}
	&__logo {
		margin-bottom: 25px;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		svg {
			width: 263px;
			height: 30px;
		}
	}
	&__dev {
		font-weight: normal;
		font-size: 12px;
		line-height: 1.27;
		color: rgba(255, 255, 255, 0.5);
	}
	&__copyright {
		padding-top: 20px;
		margin-top: 30px;
		border-top: 1px solid rgba(255, 255, 255, 0.2);
		text-align: center;
		font-weight: normal;
		font-size: 12px;
		line-height: 1.5;
		color: rgba(255, 255, 255, 0.5);
	}
}

.footer-contacts {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	margin-bottom: 15px;
	&:last-child {
		margin-bottom: 0;
	}
	&__icon {
		width: 40px;
		min-width: 40px;
		height: 40px;
		margin-right: 15px;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 50%;
		background-color: $brown;
		svg {
			width: 18px;
			height: 18px;
			color: #fff;
		}
	}
	&__text {
		text-decoration: none;
		font-weight: 500;
		font-size: 18px;
		line-height: 21px;
		color: #ffffff;
	}
	a:hover {
		text-decoration: underline;
	}
}
.social {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	align-content: flex-start;
	&__item {
		margin-right: 15px;
		&:last-child {
			margin-right: 0;
		}
	}
}
