.sales-offer {
	margin-bottom: 120px;
	min-height: 522px;
	.container {
		position: relative;
	}
	&__img {
		img {
			max-width: 938px;
			left: -308px;
			position: relative;
			top: 0;
		}
	}
	&__date {
		font-weight: 500;
		font-size: 20px;
		line-height: 34px;
		text-align: right;
		letter-spacing: 0.08em;
		text-transform: uppercase;
		color: #ffffff;
		padding: 7px 30px 8px 36px;
		background-color: $brown;
		max-width: 270px;
		clip-path: polygon(40% 0, 100% 0%, 100% 100%, 0% 100%);
		margin-bottom: 43px;
		margin-left: auto;
		.bold {
			font-size: 30px;
			font-weight: 900;
			color: $light-beige;
		}
	}
	&__title {
		font-weight: 500;
		font-size: 40px;
		line-height: 62px;
		text-align: right;
		letter-spacing: 0.08em;
		text-transform: uppercase;
		color: $text-color;
		margin-bottom: 30px;
		.bold {
			font-size: 48px;
			font-weight: 900;
			color: $brown;
		}
	}

	.btn-primary {
		margin-left: auto;
		margin-bottom: 40px;
	}
	&__notice {
		font-weight: normal;
		font-size: 14px;
		line-height: 17px;
		text-align: right;
		letter-spacing: 0.08em;
		color: $text-color;
	}
}
