.social {
	&__item {
		width: 40px;
		height: 40px;
		border-radius: 50%;
		background-color: $brown;
		display: flex;
		align-items: center;
		justify-content: center;
		align-content: center;
		padding: 11px;
		svg {
			width: 18px;
			height: 18px;
			color: #fff;
			transition: 0.3s all;
		}
		&:hover {
			svg {
				transform: scale(1.1);
			}
		}
		&_beige {
			background-color: $biege_2;
		}
		&_big {
			width: 80px;
			height: 80px;
			svg {
				width: 48px;
				height: 48px;
			}
		}
	}
}
