.reviews {
	margin-bottom: 120px;
	position: relative;

	.section-title {
		margin-bottom: 70px;
	}
	&__items {
		position: relative;
		&:after {
			content: '';
			position: absolute;
			display: block;
			z-index: 2;
			width: calc(100% - 264px);
			height: 100%;
			top: 0;
			right: 0;
			background: linear-gradient(
				90deg,
				rgba(255, 255, 255, 0) 0%,
				rgba(255, 255, 255, 0.8) 25%
			);
		}
	}
	&__item {
		// width: 262px;
		width: auto;
		max-height: 525px;
		img {
			width: auto !important;
		}
	}
	.btn-next {
		position: absolute;
		left: -90px;
		top: 50%;
	}
}
