.car-models {
	margin-bottom: 80px;
	transition: 0.3s all;
	.section-title,
	.section-title_2 {
		margin-bottom: 60px;
	}
	.application-form {
		display: none;
	}
}
.car-models-select {
	display: flex;
	align-items: center;
	justify-content: center;
	margin-bottom: 60px;
	.custom-select {
		margin-right: 30px;
		max-width: 345px;
		width: 100%;
		z-index: 5;
	}
	.btn-primary {
		padding: 15.5px 61px;
	}
}
