.stickers {
	&__item {
		width: 56px;
		height: 56px;
		position: relative;
		border-radius: radius;
		display: flex;
		align-items: center;
		justify-content: center;
		align-content: center;
		border-radius: 50%;
		background-color: $brown;
		@extend %stickers-text;
		color: #fff;
		&::after {
			content: '';
			width: 60px;
			height: 60px;
			border: 1px solid $brown;
			border-radius: 50%;
			position: absolute;
		}
	}
}
