.subscribe {
	margin-bottom: 120px;
	.section-title {
		margin-bottom: 10px;
	}
	.container {
		position: relative;
	}
	.subtitle {
		font-weight: 500;
		font-size: 24px;
		line-height: 1.17;
		text-align: center;
		color: $brown;
		max-width: 640px;
		margin-right: auto;
		margin-left: auto;
		margin-bottom: 70px;
	}
	.social {
		display: flex;
		align-items: center;
		justify-content: center;
		&__item {
			margin-right: 60px;
			&:last-child {
				margin-right: 0;
			}
		}
	}
	&__img {
		width: 370px;
		height: 344px;
		position: absolute;
		right: -90px;
		bottom: 0;
	}
}
