.scheme-item {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	text-align: center;
	&_bottom {
		margin-top: 90px;
	}
	&_top {
		margin-bottom: 130;
	}
	&__number {
		font-weight: bold;
		font-size: 80px;
		line-height: 1;
		text-align: center;
		color: rgba(202, 150, 82, 0.2);
		margin-bottom: 5px;
	}
	&__description {
		font-weight: 500;
		font-size: 16px;
		line-height: 20px;
		text-align: center;
		color: $brown;
	}
	.btn-primary {
		margin-top: 30px;
	}
}
