.doubt {
	position: relative;
	z-index: 1;
	margin-bottom: 120px;
	.section-title_3 {
		margin-bottom: 15px;
	}
	.subtitle_2 {
		margin-bottom: 40px;
	}
	.application-form {
		margin-bottom: 30px;
	}
	&-social {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		&__text {
			font-weight: 500;
			font-size: 20px;
			line-height: 24px;
			color: $brown;
			max-width: 320px;
			width: 100%;
			margin-right: 20px;
		}

		.social {
			display: flex;
			align-items: center;
			justify-content: flex-start;
			align-content: flex-start;
			&__item {
				margin-right: 20px;
				&:last-child {
					margin-right: 0;
				}
			}
		}
	}
	&__img {
		position: relative;
		width: 510px;
		left: -247px;
		img {
			width: 510px;
		}
	}
	.btn-play {
		left: 153px;
		&__caption {
			position: absolute;
			font-weight: 500;
			font-size: 12px;
			line-height: 12px;
			text-align: center;
			text-transform: uppercase;
			color: $brown;
			left: 157px;
			bottom: 0;
		}
	}

	&__decoration-img {
		position: absolute;
		right: 0;
		top: -95px;
		z-index: -1;
	}
}
