.hero {
	min-height: 700px;
	top: -90px;
	position: relative;
	margin-bottom: -90px;
	padding-top: 90px;
	background-repeat: no-repeat;
	background-position: center center;
	overflow-x: hidden;
	.container {
		position: relative;
		z-index: 5;
	}
	&__title {
		@extend %hero-title;
		max-width: 750px;
		margin-bottom: 35px;
		&_bold {
			font-size: 48px;
			font-weight: bold;
		}
	}
	&__subtitle {
		@extend %subtitle_1;
		margin-bottom: 45px;
	}
	.owl-carousel {
		.owl-stage-outer {
			padding-top: 90px;
			top: -90px;
		}
		.owl-dots {
			position: absolute;
			top: 50%;
			z-index: 10;
			display: flex;
			align-items: center;
			justify-content: flex-start;
			align-content: flex-start;
			flex-direction: column;
			transform: translateY(-50%);
			right: 5%;
			.owl-dot {
				width: 14px;
				height: 14px;
				border-radius: 50%;
				margin-bottom: 18px;
				position: relative;
				border: 1px solid #bfbebe !important;
				display: flex;
				align-items: center;
				justify-content: center;
				align-content: center;
			}

			.active {
				&::before {
					content: '';
					width: 6px;
					height: 6px;
					border-radius: 50%;
					position: absolute;
					display: block;
					background-color: $brown;
				}
				&::after {
					content: '';
					width: 22px;
					height: 22px;
					border-radius: 50%;
					border: 1px solid $brown;
					position: absolute;
					display: block;
				}
			}
		}
	}
	&-item {
		width: 100%;
		height: 100%;
		position: relative;
		margin-bottom: -90px;
		top: -90px;
		min-height: 700px;
		padding-top: 90px;
		display: flex;
		align-items: center;
		justify-content: center;
		align-content: center;
		background-repeat: no-repeat;
		background-position: center center;
		background-size: cover;

		z-index: 1;
	}
	&__decoration-text {
		font-weight: bold;
		font-size: 280px;
		line-height: 332px;
		display: flex;
		align-items: center;
		text-transform: uppercase;
		background: #65626d;
		background: linear-gradient(to right, #65626d 3%, #fbfbfb 100%);
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
		opacity: 0.03;
		position: absolute;
		z-index: 2;
		top: 50%;
		transform: translateY(-50%);
	}
	&__decoration-img {
		position: absolute;
		width: 600px;
		height: 700px;
		background-image: url('../img/hero-decoration-img.png');
		background-repeat: no-repeat;
		background-position: center center;
		background-size: 100% 100%;
		right: 0;
		z-index: 2;
		top: 0;
	}
	.btn-scroll-down {
		position: absolute;
		right: 4.2%;
		top: 65%;
		transform: translateY(-60%);
		z-index: 10;
		svg {
			color: rgba(241, 240, 244, 0.5);
		}
	}
}

.hero-actions {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	margin-bottom: 55px;
	.btn-primary {
		margin-right: 30px;
		&:last-child {
			margin-right: 0;
		}
	}
}

.hero-features {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	align-content: flex-start;
	&__card {
		max-width: 80px;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		align-content: flex-start;
		flex-direction: column;
		margin-right: 60px;
		&:last-child {
			margin-right: 0;
		}
	}
	&__icon {
		width: 30px;
		height: 30px;
		margin-bottom: 10px;
		svg {
			color: $brown;
			width: 100%;
			height: 100%;
		}
	}
	&__text {
		@extend %card-text;
	}
}
