.product-card {
	margin-bottom: 40px;
	&__img {
		overflow: hidden;
		position: relative;
		border-radius: 18px 18px 0px 0px;
		img {
			max-height: 260px;
		}
		.stickers {
			position: absolute;
			top: 10px;
			left: 10px;
			z-index: 2;
			&__item {
				margin-bottom: 10px;
				&:last-child {
					margin-bottom: 0;
				}
			}
		}
		.owl-dots {
			width: 100%;
			margin-bottom: 20px;
			margin-top: 10px;
			display: flex;
			flex-wrap: nowrap;
			flex-direction: row;
			.owl-dot {
				background-color: #bbbdc2 !important;
				height: 3px;
				width: 100%;
				transition: 0.3s all;
			}
			.active {
				background-color: $brown !important;
			}
		}
	}

	&__title {
		display: block;
		@extend %card-title_1;
		text-decoration: none;
		margin-bottom: 30px;
		transition: 0.3s all;
		&:hover {
			text-decoration: underline;
		}
	}
	.owl-nav {
		display: none;
	}
}

.product-card-bottom {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	flex-wrap: wrap;
	.price {
		&__old {
			font-weight: 500;
			font-size: 16px;
			line-height: 19px;
			text-decoration: line-through;
			color: $grey;
			margin-bottom: 10px;
		}
		&__current {
			font-weight: bold;
			font-size: 22px;
			line-height: 26px;
			color: $brown;
			margin-bottom: 10px;
		}
	}
}
