.instagram {
	margin-bottom: 120px;
	.subtitle {
		font-style: italic;
		font-weight: 500;
		font-size: 30px;
		line-height: 1.93;
		text-align: center;
		color: $brown;
		margin-bottom: 70px;
	}

	&__item {
		width: 263px;
		height: 263px;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-bottom: 30px;
		background-repeat: no-repeat;
		background-position: center center;
		background-size: cover;
		text-decoration: none;
		color: #fff;
		position: relative;
		&:hover {
			.instagram__item-gradient {
				opacity: 1;
				visibility: visible;
			}
			.instagram__info-wrapper {
				opacity: 1;
				visibility: visible;
			}
		}
		&_big {
			width: 100%;
		}

		.btn-play {
			transform: none;
		}
	}
	&__item-gradient {
		background: rgba(82, 68, 64, 0.8);
		position: absolute;
		width: 100%;
		height: 100%;
		display: block;
		z-index: 1;
		opacity: 0;
		visibility: hidden;
		transition: 0.3s all;
	}

	&__info-wrapper {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		position: relative;
		z-index: 2;
		opacity: 0;
		visibility: hidden;
		transition: 0.3s all;
	}
	&__info {
		margin-right: 30px;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		&:last-child {
			margin-right: 0;
		}
	}
	&__info-icon {
		margin-right: 5px;
		width: 18px;
		height: 18px;
		svg {
			width: 100%;
			height: 100%;
			color: #fff;
		}
	}
	&__info-numbers {
		font-weight: 500;
		font-size: 15px;
		line-height: 18px;
		text-align: center;
		text-transform: uppercase;
		color: #ffffff;
	}
}
