%hero-title {
	font-weight: normal;
	font-size: 46px;
	line-height: 62px;
	letter-spacing: 0.08em;
	text-transform: uppercase;
	color: $text-color;
}

%section-title {
	font-weight: bold;
	font-size: 38px;
	line-height: 1.53;
	text-align: center;
	letter-spacing: 0.08em;
	text-transform: uppercase;
	color: $text-color;
	&::before {
		content: '';
		width: 165px;
		height: 5px;
		margin-bottom: 10px;
		display: block;
		background-color: $brown;
		margin-left: auto;
		margin-right: auto;
	}
}
%section-title_2 {
	font-weight: bold;
	font-size: 32px;
	line-height: 1.81;
	text-align: center;
	letter-spacing: 0.08em;
	text-transform: uppercase;
	color: $brown;
}
%section-title_3 {
	font-weight: bold;
	font-size: 42px;
	line-height: 1.38;
	letter-spacing: 0.08em;
	text-transform: uppercase;
	color: $text-color;
	&::before {
		content: '';
		width: 165px;
		height: 5px;
		margin-bottom: 10px;
		display: block;
		background-color: $brown;
		margin-right: auto;
		margin-left: 0;
	}
}

%card-title_1 {
	font-weight: bold;
	font-size: 20px;
	line-height: 24px;
	text-align: center;
	text-transform: uppercase;
	color: $text-color;
}

%card-title_2 {
}
/////////////////////

// subtitle
%subtitle_1 {
	font-weight: 500;
	font-size: 18px;
	line-height: 30px;
	color: $brown;
}
%subtitle_2 {
	font-weight: normal;
	font-size: 20px;
	line-height: 1.4;
	color: $brown;
}
////////////////////

// text
%card-text {
	font-weight: normal;
	font-size: 12px;
	line-height: 14px;
	text-align: center;
	text-transform: uppercase;
	color: #000000;
}

%card-text_2 {
	font-weight: normal;
	font-size: 14px;
	line-height: 20px;
	text-align: center;
	color: $brown;
}

%btn-text {
	font-weight: normal;
	font-size: 16px;
	line-height: 19px;
	text-transform: uppercase;
}
%btn-text_2 {
	font-weight: 500;
	font-size: 12px;
	line-height: 14px;
	text-transform: uppercase;
}

%input-text {
	font-weight: normal;
	font-size: 12px;
	line-height: 14px;
	color: $brown;
}

%header-text {
	font-weight: normal;
	font-size: 14px;
	line-height: 17px;
	text-transform: uppercase;
	color: $brown;
}

%stickers-text {
	font-family: $secondary-font;
	font-weight: bold;
	font-size: 18px;
	line-height: 34px;
	text-align: center;
	color: #ffffff;
}
// ul
%ul-reset {
	list-style: none;
	padding: 0;
	margin: 0;
}
//////////////////
