.modal {
	display: none;
	&_big {
		max-width: 1140px;
		width: 100%;
	}
	&_small {
		max-width: 432px;
		width: 100%;
	}
	&_middle {
		max-width: 587px;
		width: 100%;
	}
	&__wrapper {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
	}
	.product-card__img {
		width: 525px;
		min-width: 525px;
		height: auto;
		margin-right: 30px;
		img {
			// width: auto !important;
			height: 100%;
			max-height: none;
		}
		.owl-nav {
			display: flex;
			align-items: center;
			justify-content: space-between;
			position: absolute;
			height: 0;
			top: 50%;
			left: 10px;
			width: calc(100% - 20px);
			transform: translateY(-50%);
			button {
				width: 37.5px;
				display: flex;
				align-items: center;
				justify-content: center;
				align-content: center;
				height: 37.5px;
				border-radius: 50%;
				position: relative;
				background-color: rgba(202, 150, 82, 0.8) !important;
				svg {
					color: #000;
					transition: 0.3s all;
				}
				&::after {
					content: '';
					background-repeat: no-repeat;
					background-position: center center;
					background-size: 100% 100%;
					background-image: url(../img/line-btn_3.svg);
					position: absolute;
					width: 44px;
					height: 44px;
					top: -2px;
					left: -7px;
					transition: 0.3s all;
				}

				&:hover {
					svg {
						color: #fff;
					}
					&::after {
						top: -4px;
						left: 0;
						transform: rotate(180deg);
					}
				}
			}
		}
	}
	&__title {
		font-weight: bold;
		font-size: 24px;
		line-height: 28px;
		text-transform: uppercase;
		color: $text-color;
		margin-bottom: 20px;
	}
	&__price {
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		padding-bottom: 10px;
		border-bottom: 1px solid #bbbdc2;
		margin-bottom: 30px;
	}
	&__price-caption {
		font-weight: 500;
		font-size: 18px;
		line-height: 21px;
		color: $text-color;
	}
	&__price-wrapper {
		text-align: right;
	}
	&__price-current {
		font-weight: bold;
		font-size: 24px;
		line-height: 28px;
		color: $brown;
	}
	&__price-old {
		font-weight: 500;
		font-size: 16px;
		line-height: 19px;
		text-align: right;
		text-decoration: line-through;
		color: #bbbdc2;
	}

	.section-title {
		margin-bottom: 15px;
	}
	.subtitle {
		max-width: 400px;
		margin-right: auto;
		margin-left: auto;
		font-weight: 500;
		font-size: 18px;
		line-height: 1.33;
		text-align: center;
		color: $brown;
		margin-bottom: 40px;
	}

	.price {
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		border-bottom: 1px solid #bbbdc2;
		padding-bottom: 15px;
		margin-bottom: 40px;
		&__caption {
			font-weight: 500;
			font-size: 20px;
			line-height: 24px;
			color: $text-color;
		}
		&__wrapper {
			text-align: right;
		}
		&__old {
			font-weight: 500;
			font-size: 24px;
			line-height: 28px;
			text-decoration: line-through;
			color: #bbbdc2;
		}
		&__current {
			font-weight: bold;
			font-size: 34px;
			line-height: 40px;
			color: $brown;
		}
	}
}
.modal-product {
	&__description {
		width: 100%;
	}
	&__title {
		font-weight: bold;
		font-size: 40px;
		line-height: 47px;
		text-transform: uppercase;
		color: $text-color;
		margin-bottom: 20px;
	}
	&__list {
		margin: 0;
		padding: 0;
		list-style: none;
		margin-bottom: 40px;
	}
	&__list-item {
		font-weight: normal;
		font-size: 18px;
		line-height: 21px;
		color: #000000;
		padding-left: 40px;
		position: relative;
		margin-bottom: 15px;
		&::before {
			content: '';
			width: 20px;
			height: 20px;
			background-image: url(../img/check.svg);
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 100% 100%;
			position: absolute;
			top: 0px;
			left: 0;
		}
	}
	&__notice {
		font-weight: normal;
		font-size: 14px;
		line-height: 17px;
		color: #000000;
		margin-bottom: 40px;
	}
	&__btns {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		.btn-primary {
			margin-right: 20px;
			&:last-child {
				margin-right: 0;
			}
		}
	}
}

.modal-form {
	.input {
		margin-bottom: 35px;
	}
	.custom-select {
		background: rgba(255, 255, 255, 0.5);
		border-radius: 20px;
		.select-selected {
			background: rgba(255, 255, 255, 0.5);
			border-radius: 20px;
			padding: 20px;
			border: 2px solid #524440;
			&::after {
				top: 20px;
			}
		}
	}
	&__checkbox-caption {
		font-weight: normal;
		font-size: 14px;
		line-height: 17px;
		color: $text-color;
		margin-bottom: 15px;
	}
	.custom-checkbox {
		margin-bottom: 25px;
	}
	&__present {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		align-content: flex-start;
		margin-bottom: 40px;

		img {
			margin-right: 20px;
		}
	}
	&__present-text {
		font-weight: normal;
		font-size: 14px;
		line-height: 17px;
		text-transform: uppercase;
		color: $text-color;
	}
}

.fancybox-content {
	padding: 30px;
	border-radius: 18px;
}
