.custom-select {
	position: relative;
	font-family: Arial;
	select {
		display: none;
	}

	.select-selected {
		background-color: #fff;
		color: $brown;
		padding: 15px 40px 15px 20px;
		border: 1px solid $brown;
		border-radius: 20px;
		z-index: 10;
		&::after {
			transition: 0.3s all;
			content: '';
			width: 20px;
			height: 20px;
			position: absolute;
			top: 15px;
			right: 20px;
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 100% 100%;
			background-image: url(../img/dropdown-arrow.svg);
			cursor: pointer;
		}
	}
	.select-arrow-active::after {
		transform: rotate(180deg);
	}
	.select-items {
		border-radius: 20px;
		background-color: #fff;
		border: 1px solid $brown;
		position: absolute;
		width: 100%;
		overflow: hidden;
		z-index: 20;
		> div {
			padding: 5px 20px;
			cursor: pointer;
		}
	}

	.select-hide {
		display: none;
	}
	.select-items div:hover,
	.same-as-selected {
		background-color: rgba(0, 0, 0, 0.1);
	}

	&__caption {
		position: absolute;
		background-color: #fff;
		left: 35px;
		top: -12px;
		padding: 5px 8.5px;
		font-weight: normal;
		font-size: 14px;
		line-height: 17px;
	}
}

.input {
	position: relative;
	width: 100%;
	&__caption {
		position: absolute;
		background-color: #fff;
		left: 35px;
		top: -12px;
		padding: 5px 8.5px;
		font-weight: normal;
		font-size: 14px;
		line-height: 17px;
	}
	input,
	textarea {
		width: 100%;
		padding: 20px;
		border-radius: 20px;
		border: 2px solid $brown;
		outline: none;
		@extend %input-text;
		resize: none;
	}
}

.custom-checkbox {
	display: block;
	position: relative;
	cursor: pointer;
	font-size: 22px;
	user-select: none;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	font-weight: normal;
	font-size: 14px;
	line-height: 17px;
	color: $text-color;
	input {
		display: none;
		position: absolute;
		opacity: 0;
		cursor: pointer;
		height: 0;
		width: 0;
	}
	.checkmark {
		top: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		align-content: center;
		left: 0;
		height: 20px;
		width: 20px;
		min-width: 20px;
		background-color: #fff;
		margin-right: 10px;
		border: 1px solid #bbbdc2;
		border-radius: 2px;
		&::after {
			content: '';
			position: absolute;
			display: none;
			background-image: url(../img/check.svg);
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 100% 100%;
			width: 16px;
			height: 16px;
		}
	}
	input:checked ~ .checkmark:after {
		display: block;
	}
}
